/* Hero block */
.home {
  height: 85vh;
  overflow: hidden;
  position: relative;
  /* margin-top: 5.5rem; */
  background-image:url("./Fondogtav.jpg");
  background-size:100% 100%;
  background-repeat: no-repeat;
}
/* hero intro text element */
.intro__text {
  position: relative;
  z-index: 2;  
  margin-top: 150px;
  /* display: grid;
  grid-template: 100vh / minmax(100px, auto); */
  display: flex;
  flex-direction: row;
  justify-content: center; 
  align-self: initial;
}
.intro-text__container button{
  height: 50px;
  width: 287px;
  color: white;
  background:#242348 ;
  border:#242348 ;
  font-size: 22px;
  font-family: 'Open Sans', Roboto;
  border-radius: 5px;
}
.intro-text__container a{
 margin: 0px; 
}
.intro-text__Button{
  background: blue;
  text-decoration: none;
  border: none;
  color: white;
  padding: 9px 35px;
  border: 0px 20px;
  font-size: 2em;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer;
  font-family: cursive;
  font-weight: bold;
  border-radius: 5px;
 }
 .logo{
  height: 30px;
  width: 35px;
  margin-bottom: 0px;
  margin-top: 0px;
 }
 .opcional{
  font-size: 7rem;
}
.intro-text__paragraph {
  color: rgb(255, 255, 255);  
  margin-bottom: 0px;
  margin-top: 0px;
  text-align:center;
  font-size: 5rem;
  text-shadow: 2px 2px 2px blue;
  font-family: "Vesper Libre", serif;
}
.intro-text__paragraph2 {
  color: rgb(255, 255, 255);  
  margin-bottom: 0px;
  margin-top: 0px;
  text-align:center;
  font-size: 8rem;
  text-shadow: 2px 2px 2px blue;
  font-family: "Vesper Libre", serif;
}
@import url("https://fonts.googleapis.com/css2?family=Vesper+Libre:wght@900&display=swap");
.roow{
  margin-top: 70px;
  flex-direction: row;
  justify-content: center; 
  align-self: initial;
  display: flex;
  margin-right: 50px;
  flex-flow: row wrap;
}
.roow2{
  margin-right: 50px;
}

@media screen and (max-width:900px){
  .hero {
    background-image: url("./Fondogtavm.jpg");
    background-size: 100% 100%;
    height: 20vh;
    overflow: hidden;
    position: relative;
    margin-top: 0px;
    padding-top: 4.5rem;
  }
  .intro-text__paragraph {
    color: white;
    font-size: 5rem;
  text-shadow: 2px 2px 2px blue;
    text-align:center;
    font-family: "Vesper Libre", serif;
    margin-left: 10px;
  }
  .intro-text__paragraph2 {
    color: white;
    font-size: 6rem;
    text-align:center;
  text-shadow: 2px 2px 2px blue;

    font-family: "Vesper Libre", serif;
    margin-left: 10px;
  }
  
  .intro-text__container button{
    height: 25px;
    width: 100px;
    color: white;
    background: #242348;
    border: #242348;
    font-size: 9px;
    font-family: cursive;
  }
   .intro-text__Button{
    padding: 15px 30px;
    margin-right: 25px;
    font-size: 3em;
   }
   
    .roow{
  margin-top: 50px;
  flex-direction: row;
  justify-content: center; 
  align-self: initial;
  display: flex;
  margin-right: 0px;
  flex-flow: row wrap;
}
}
