.comprobante{
    background: #333;
    color: white;
    /* padding: 1.2em; */
    /* display: grid;
    grid-template: 1fr  / repeat(2,1fr); */
    justify-items: center;
    align-items: center;
    display: flex;
    flex-direction: row wrap;
    justify-content: space-around;
}
.roow{
    justify-items: center;
    align-items: center;
    display: flex;
    flex-direction: row wrap;
    justify-content: space-around;
    margin-top: 0px;
    
}
.btn {
    padding: 9px 25px;
}
.logoA {
    height: 60px;
    width: 160px;
}
.logoP {
    height: 60px;
    width: 160px;
}
.comprobante__text{
    font-family: cursive;
    font-size: 3em;
    font-weight: bold;
    margin-left: 10px;
    color: white;
    margin-right: 10px;
}

@media screen and (max-width: 600px) {
    .comprobante__text{
        font-size: 35px;        
    }
    .btn {
        padding: 10px 15px;
    }
    .comprobante{
        grid-template: 1fr / 62% 1fr;
    }
    
}