@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');
@import url("https://fonts.googleapis.com/css2?family=Vesper+Libre:wght@900&display=swap");
.cardInformation{
    /* display: grid;
    grid-template: repeat(1, minmax(200px, 1fr))/ 80px 200px; */
    display: flex;
    background: #FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.20);
    overflow: hidden;
    position: relative;
    justify-items: center;
    margin:10px;
    height: 200px;
    width: 300px;
    
}
.imagen{
    width: 100%;
}
.cardInformation_ico{
    align-self: center;
    margin: 10px;
}
.nosotros__text{
    font-family: "Vesper Libre", serif;
    font-size: 1.1rem;
    margin-right: 12px;
}

*{
	margin:0px;
	padding:0px;
	font-family: helvetica;
}

div.contenedor{
	margin:10px;
    height: auto;
    width: 300px;
	overflow: hidden;
}

div.imagen{
	width:300px;
	height:200px;
	background-color: #cecece;
	overflow: hidden;
	-webkit-transition: height .4s;
}

img.imagen{
	display: block;
	margin:0px auto;
	width:300px;
	height:200px;
	-webkit-transition: margin-top .4s;
}

div.info{
	width:280px;
	height:0px;
    background-color: #233c80;
	overflow: hidden;
	-webkit-transition: height .4s;
}

p.titulo{
	margin:15px;
	font-size: 1.6em;
    color:#d53a61;
    margin-left: 15px;
    margin-right: 15px;
    text-align: center;
    align-items: center;
    font-family: cursive;
}

p.adicional{
	color:white;
    margin-left: 15px;
    margin-right: 15px;
    text-align: center;
    align-items: center;
    font-family: cursive;
}

a.link{
	float:right;
	margin-top:-25px;
	margin-right:5px;
	padding:10px 30px;
	background-color:#d53a61;
	font-size: .8em;
	color:white;
	text-decoration: none;
}

div.contenedor:hover div.imagen{
	height: 00px;
}

div.contenedor:hover div.info{
	height:210px;
}

div.contenedor:hover img.imagen{
	margin-top:1px;
}


@media screen and (max-width : 900px){
    .cardInformation{
        /* grid-template: repeat(1, minmax(200px, 1fr)) / 50px auto; */
        width: 290px;
    }
    .nosotros__text{

        font-size: 1.8rem;
    }
    .subtitle__text{
        font-size: 2rem;
    }
    p.titulo{
        font-size: 2.6em;
    }
    p.adicional{
        font-size: 2em;
    }
  }
